import React from 'react';
import './App.css';
import {HashRouter, Route} from 'react-router-dom';
import Main from './containers/main';
import AboutPage from './containers/about/aboutPage/aboutPage';
import ToursPage from './containers/tours&travels/toursPage/tourspage';
import DestinationPage from './containers/destinations/destiantionsPage/destinationpage';
import ImageViewer from './containers/destinations/destiantionsPage/imageViewer';

function App() {
  return (
    <HashRouter basename="/">
      <Route path="/" exact component={Main}/>
      <Route path="/about" exact component={AboutPage}/>
      <Route path="/tours" exact component={ToursPage}/>      
      <Route path="/destinations" exact component={DestinationPage}/>
      <Route path={"/image/:id"} exact component={ImageViewer}/>
    </HashRouter>
  );
}

export default App;
