import React from 'react';
import './home.css';
import makeCarousel from 'react-reveal/makeCarousel';
import {Slide, Fade} from 'react-reveal';
import {images} from './carouselContainer';


function Home() {
    const CarouselUI = ({position, handleClick, children}) => <section className="home">
                                                                    {children}

                                                                    <div className="introTitle">
                                                                        <Fade left distance="20px">
                                                                            <h1>Hit The Road</h1>
                                                                            <p>Get to experience the serenity of nature</p>
                                                                        </Fade>
                                                                    </div>

                                                                    <span className="dot">
                                                                        <button onClick={handleClick} data-position={position - 1}><img src={require('../../img/icon-prev.svg')} alt="Previous slide button"/></button>
                                                                            {images.map((image, index)=><div key={index} className={index===position ? 'dots activeCarousel' : 'dots'} onClick={handleClick} data-position={index}></div>)}
                                                                        <button onClick={handleClick} data-position={position + 1}><img src={require('../../img/icon-next.svg')} alt="Next slide button"/></button>
                                                                    </span>                                                            
                                                                </section>;
    const Carousel = makeCarousel(CarouselUI);
    return (
                <Carousel defaultWait={5000} maxTurns={10}>                    
                        {images.map((image,index)=><Slide duration={1000} right>{image.type === "image" ? <img key={index} className="img" src={require(`../../img/${image.name}`)} alt=""/> : <video className="video" src={require(`../../img/${image.name}`)} autoPlay muted></video>}</Slide>)}                    
                </Carousel> 
    );
}

export default Home;