import React from 'react';
import {Fade} from 'react-reveal';
import './about.css';

function About() {
    return (
        <section className="about">
            <header>
                <Fade right distance="20px">
                    <h1>What We Do</h1>
                    <div className="underline"></div>
                </Fade>
            </header>
            <div className="about-container">
                <div>
                    <Fade left distance="20px"><img src={require('../../img/undraw_navigator_a479.png')} alt=""/></Fade>
                    <Fade right distance="20px"><p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p></Fade>
                </div>
                <div className="b">
                    <Fade left distance="20px"><p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p></Fade>
                    <Fade right distance="20px"><img src={require('../../img/undraw_adventure_4hum.png')} alt=""/></Fade>
                </div>
            </div>
            
        </section>
    );
}

export default About;