import React from 'react';
import {Images} from '../data';
import { NavLink } from 'react-router-dom';

function Gallery() {
    
    return (
        <div className="gallery">
            <h2>Sharing the Moments</h2>
            <p>Nullam auctor, sapien sit amet lacinia euismod, lorem magna lobortis massa, in tincidunt mi metus quis lectus. Duis nec lobortis velit. Vivamus id magna vulputate, tempor ante eget, tempus augue. Maecenas ultricies neque magna.</p>
            <hr/>
            <div className="photos">
                {Images.map((image, index)=><NavLink key={index} to={`/image/${index}`}><img src={require(`../../../../img/${image.name}`)} alt=""/></NavLink>)}
            </div>
            <hr/>
        </div>
    );
}

export default Gallery;