import React from 'react';
import {Fade} from 'react-reveal';
import './contact.css';

function ContactSection() {
    return (
        <div className="contact-section">
            <header>
                <Fade right distance="20px">
                    <h1>Get in touch</h1>
                    <div className="underline"></div>
                </Fade>
            </header>
            <div className="formContainer">
                <div className="formBox">
                    <div className="a">
                        <Fade left distance="20px">
                            <img src={require('../../img/undraw_directions_x53j.png')} alt=""/>
                        </Fade>
                    </div>
                    <form>
                        <div>
                            <label>Email</label>
                            <input type="text"/>
                            <label>Phone</label>
                            <input type="text"/>
                            <label>Your message</label>
                            <input type="text"/>
                            <input type="submit" value="Send"/>
                        </div>
                    </form>
                    <div className="map">
                        <div className="mapHolder"></div>
                        <div className="mapDetails">
                            <Fade left distance="10px"><p>Nairobi, Kenya<br/>Santaneu Plaza, Empty Road<br/>55 GMT<br/>EastSide, opposite BestMades on Ngara Road</p></Fade>
                        </div>
                    </div>
                </div>
            </div>
            <div className="socialMedia">
                <Fade left distance="20px">
                    <div></div>
                    <div></div>
                </Fade>
                <Fade right distance="20px">
                    <div></div>
                    <div></div>
                </Fade>
            </div>
            
        </div>
    );
}

export default ContactSection;