import React from 'react';
import {NavLink} from 'react-router-dom';
import './footer.css';

function Footer() {
    return (
        <div className="footer">
            <div className="fcontainer">
                <div>
                    <h2>Overcrunch</h2>
                    <p>Where the world goes for premium services</p>
                </div>
                <div>
                    <h2>Using Overcruch</h2>
                    <ul>
                        <li>Order custom decos</li>
                        <li>Track My Order</li>
                    </ul>
                </div>
                <div>
                    <h2>Resources</h2>
                    <ul>
                        <li>Help center</li>
                        <li>Pricing</li>
                        <li>Blog</li>
                    </ul>
                </div>
                <div>
                    <h2>Company</h2>
                    <ul>
                        <li>About</li>
                        <li>Privacy</li>
                        <li>Contact</li>
                    </ul>
                </div>
                <div>
                    <h2>Follow Us</h2>
                    <ul>
                        <li>Facebook</li>
                        <li>Twitter</li>
                        <li>Instagram</li>
                    </ul>
                </div>
            </div>
            <div className="designers">
                <p>Designed with <span>Heart</span> at <NavLink  to="/">Youngrammers.inc</NavLink></p>
            </div>
            
        </div>
    );
}

export default Footer;