import React from 'react';
import './destinationSect.css';
import {Fade} from 'react-reveal';
import {destinations} from '../tours&travels/data';

function DestinationSection() {
    return (
        <section className="destination-section">
            <header>
                <Fade duration={1000} right distance="20px">
                    <h1>Destinations</h1>
                    <div className="underline"></div>
                </Fade>
            </header>
            <div className="destination-container">
                <div className="destination-ad">
                    <Fade left distance="20px"><img src={require('../../img/undraw_destinations_fpv7.png')} alt=""/></Fade>
                    <Fade right distance="20px"><p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p></Fade>
                </div>
            </div>
            <Fade right distance="20px"><span className="dpara"><p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod</p></span></Fade>
            <div className="destination-gallery">
                {destinations.map(image=><Fade bottom distance="20px"><img src={require(`../../img/${image.image}`)} alt=""/></Fade>)}
            </div>
        <hr/>
        </section>
    );
}

export default DestinationSection;