import React, { useState } from 'react';
import {Mombasa, Nakuru, Kisumu} from '../images';
// import {Slide} from 'react-reveal';

function Destiny() {
    const mombasa = Mombasa.length - 1;
    const [pos, updatePos] = useState(mombasa);
    const Next = () => {
        if(pos === mombasa){
            updatePos(0);
        }else{
            updatePos(pos + 1)
        }
    };
    const Prev = () => {
        if(pos === mombasa - mombasa){
            updatePos(mombasa);
        }else{
            updatePos(pos - 1)
        }
    };

    const nakuru = Nakuru.length - 1;
    const [posi, updatePosi] = useState(nakuru);
    const NakuruNext = () => {
        if(posi === nakuru){
            updatePosi(0);
        }else{
            updatePosi(posi + 1)
        }
    };
    const NakuruPrev = () => {
        if(posi === nakuru - nakuru){
            updatePosi(nakuru);
        }else{
            updatePosi(posi - 1)
        }
    };

    const kisumu = Kisumu.length - 1;
    const [posit, updatePosit] = useState(kisumu);
    const KisNext = () => {
        if(posit === kisumu){
            updatePosit(0);
        }else{
            updatePosit(posit + 1)
        }
    };
    const KisPrev = () => {
        if(posit === kisumu - kisumu){
            updatePosit(kisumu);
        }else{
            updatePosit(posit - 1)
        }
    };
    return (
        <div className="destiny">
            <div className="destiantion-container">
                <div className="imageholder">
                    <img src={require(`../../../../img/${Mombasa[pos].name}`)} alt=""/>
                    <div className="dbtns">
                        <button onClick={Prev}><img src={require('../../../../img/icon-prev.svg')} alt=""/></button>
                        <button onClick={Next}><img src={require('../../../../img/icon-next.svg')} alt=""/></button>
                    </div>
                </div>
                <div className="description">
                    <div>
                        <h2>Mombasa's most visited places</h2>
                        <p>Aenean in lacus nec dolor fermentum congue. Maecenas ut velit pharetra, pharetra tortor sit amet, vulputate sem. Vestibulum mi nibh, faucibus ac eros id, sagittis tincidunt velit. Proin interdum ullamcorper faucibus. Ut mi nunc, sollicitudin non pulvinar id, sagittis eget diam.</p>
                    </div>
                </div>
            </div>
            <div className="destiantion-container alt">
                <div className="imageholder">
                    <img src={require(`../../../../img/${Nakuru[posi].name}`)} alt=""/>
                    <div className="dbtns altbtn">
                        <button onClick={NakuruPrev}><img src={require('../../../../img/icon-prev.svg')} alt=""/></button>
                        <button onClick={NakuruNext}><img src={require('../../../../img/icon-next.svg')} alt=""/></button>
                    </div>
                </div>
                <div className="description altdescr">
                    <div>
                        <h2>Nakuru's most visited places</h2>
                        <p>Aenean in lacus nec dolor fermentum congue. Maecenas ut velit pharetra, pharetra tortor sit amet, vulputate sem. Vestibulum mi nibh, faucibus ac eros id, sagittis tincidunt velit. Proin interdum ullamcorper faucibus. Ut mi nunc, sollicitudin non pulvinar id, sagittis eget diam.</p>
                    </div>
                </div>
            </div>
            <div className="destiantion-container">
                <div className="imageholder">
                    <img src={require(`../../../../img/${Kisumu[posit].name}`)} alt=""/>
                    <div className="dbtns">
                        <button onClick={KisPrev}><img src={require('../../../../img/icon-prev.svg')} alt=""/></button>
                        <button onClick={KisNext}><img src={require('../../../../img/icon-next.svg')} alt=""/></button>
                    </div>
                </div>
                <div className="description">
                    <div>
                        <h2>Kisumu's most visited places</h2>
                        <p>Aenean in lacus nec dolor fermentum congue. Maecenas ut velit pharetra, pharetra tortor sit amet, vulputate sem. Vestibulum mi nibh, faucibus ac eros id, sagittis tincidunt velit. Proin interdum ullamcorper faucibus. Ut mi nunc, sollicitudin non pulvinar id, sagittis eget diam.</p>
                    </div>
                </div>
            </div>
                        
        </div>
    );
}

export default Destiny;