import React from 'react';
import {Fade} from 'react-reveal';

function Testimonials() {
    return (
        <div className="testimonialSection">
            <header>
                <Fade right distance="20px">
                    <h1>What they've Said</h1>
                    <div className="underline"></div>
                </Fade>
            </header>
            <div className="testimonies">
                <Fade right distance="20px">
                    <div className="user">
                        <div className="profile">
                            <img src={require('../../../../img/IMG-20200622-WA0018.jpg')} alt=""/>
                            <p>Annie Bombanie</p>
                        </div>
                        <span>Laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor.</span>
                    </div>
                    <div className="user">
                        <div className="profile">
                            <img src={require('../../../../img/IMG-20200622-WA0015.jpg')} alt=""/>
                            <p>James Bond</p>
                        </div>
                        <span>Laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor.</span>
                    </div>
                    <div className="user">
                        <div className="profile">
                            <img src={require('../../../../img/IMG-20200622-WA0019.jpg')} alt=""/>
                            <p>Nikola Tesla</p>
                        </div>
                        <span>Laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor.</span>
                    </div>
                </Fade>
            </div>         
        </div>
    );
}

export default Testimonials;