import React from 'react';
import {Fade} from 'react-reveal';

function TeamSect() {
    return (
        <div className="teamSection">
            <header>
                <Fade right distance="20px">
                    <h1>Our team</h1>
                    <div className="underline"></div>
                </Fade>
            </header>
            <div className="team-container">
                <Fade right distance="20px">
                    <div className="member">
                        <img src={require('../../../../img/image-john.jpg')} alt=""/>
                        <p>
                            John Sawe<br/>
                            <span>Manager</span>
                        </p>
                    </div>
                    <div className="member">
                        <img src={require('../../../../img/avatar-ali.png')} alt=""/>
                        <p>
                            Ali Heda<br/>
                            <span>Treasurer</span>
                        </p>
                    </div>
                    <div className="member">
                        <img src={require('../../../../img/image-tanya.jpg')} alt=""/>
                        <p>
                            Tanya Turi<br/>
                            <span>Orgerniser</span>    
                        </p>
                    </div>
                    <div className="member">
                        <img src={require('../../../../img/avatar-anisha.png')} alt=""/>
                        <p>
                            Anisha Nd<br/>
                            <span>Lead Driver</span>
                        </p>
                    </div>
                    <div className="member">
                        <img src={require('../../../../img/avatar-shanai.png')} alt=""/>
                        <p>
                            Shanai Tae<br/>
                            <span>Lead Security</span>
                        </p>
                    </div>
                    <div className="member">
                        <img src={require('../../../../img/avatar-richard.png')} alt=""/>
                        <p>
                            Richie Brian<br/>
                            <span>Lead Guide</span>
                        </p>
                    </div>
                </Fade>                
            </div>
            <div className="pledge">
                <Fade right distance="20px">
                    <h3>What we pledge</h3>
                    <p>Laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla</p>
                </Fade>
            </div>
        </div>
    );
}

export default TeamSect;